*{
    margin: 0px;
}
.body{
    box-sizing: border-box;
}
.main-bg{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #E1EBEE;
}
.small-card{
    display: flex;
    flex-direction: row;
    height: 180px;
    width: 60%;
    align-items: center;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-top: 32px;
    border: 2px grey solid;
    background-color: white;
}

.text-bg{
    display: flex;
    flex-direction: column;
    padding-left: 100px;
}
.image{
    height: 180px;
    width: 240px;
    border-top-left-radius: 18px;
    border-bottom-left-radius: 18px;
}
.line2{
    height: 2px;
    background-color: black;
    border: 0px;
}
.para{
    font-family: 'Special Elite', cursive;
    font-size: 18px;
    font-weight: 500;
    padding-top: 10px;
    padding-bottom: 10px;
}

.headi{
    font-family: nunito;
    font-size: 20px;
}
.btn{
    width: 90px;
    height: 28px;
    margin-right: 30px;
}
.btn-bg{
    display: flex;
    flex-direction: row;
    
    
}
.heading1{
    font-size: 24px;
    margin-top: 30px;
    font-family: 'Special Elite', cursive; 
    margin-bottom: 0px;
}
.confirm-para{
    color: green;
    font-weight: 400;
    margin-top: 10px;
}

@media (max-width: 768px){
    .main-bg{
        margin-left: 0px;
        padding-left: 10px;
    }
    .heading1{
        margin: 35px 0px 0px 0px;
        font-family: cursive;
        font-size: 22px;
        margin-bottom: 8px;


    }
   
    .small-card{
        height: 380px;
        width: 80vw;
        flex-direction: column;
        margin-top: 0px;
        margin-bottom: 25px;
    }
    .image{
        height: 220px;
        width: 80vw;
        border-top-right-radius: 20px;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;

    }
    .text-bg{
        padding: 20px;
        padding-left: 20px;
        
    }
    .btn-bg{
        display: flex;
        flex-direction: row;
        
        
    }
    .headi{
        font-size: 20px;
    }
    .para{
        font-size: 12px;
        width: 70vw;
        font-size: 500;
        color: grey;
    }
    .btn{
        margin-right: 10px;
        
    }
    .confirm-para{
        margin-top: 0px;
    }
    .form{
        margin-left: 0px;
    }
    .submit-btn{
        margin-top: 0px;
        margin-bottom: 30px;
        margin-left: 46px;
        border-radius: 11px;
    }
}
.underline-div{
    display: flex;
    flex-direction: row;
    margin-top: 30px;
    margin-bottom: 0px;
}
.pred-div{
    
    margin-left: 18px;

}
.pred{
    font-weight: 500;
    font-size: 14px;
    font-family:'Special Elite', cursive;
    
    
    
}
.pred1{
    color: blue;
}
.line{
    background-color: blue;
    height: 2px;
    border: 0px;
}

@media (min-width: 768px){
    .headi{
        margin-top: 10px;
    }
    .pred{
        font-size: 18px;
    }
    .para{
        font-size: 15px;
        color: grey;
        margin-right: 10px;
    }
    .line .line2{ 
        height: 1px;
    }
    .main-bg{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .form{
        margin-left: 400px;
    }
    .submit-btn{
        margin-left: 200px;
        margin-top: 20px;
        margin-bottom: 40px;
    }
}
.submit-btn{
    width: 120px;
    height: 35px;
    background-color: #007FFF;
    color: white;
    border: 0px;
    border-radius: 4px; 
    cursor: pointer;

}
.vote{
    background-color: 	#50C878;
    width: 90px;
    border: 0px;
    border-radius: 4px;
    font-weight: 600;
    letter-spacing: 1px;
    cursor: pointer;
}
.view{
    
    width: 90px;
    border: 2px solid 	#50C878;
    border-radius: 4px;
    font-weight: 600;
    background-color: white;
    cursor: pointer;
    
}
/*
  my css 

*/
.viewdiv{
    display: none;
    position: relative;
  
   
}
.contain.viewdiv{
    display: block;
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color:black;
    top:0%;
    left:0%;
    display:flex;
    align-items: center;
    justify-content: center;
}

.close{
    color:white;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    position: absolute;
    font-size: large;
    font-weight: 1000;
    font-style:inherit;
    right:100px;
    top:100px;
    width: 20px;
    height: 22px;
    text-align: center;
    cursor: pointer;
    border-radius: 2px;
}
.close:hover{
    color:red;
    background-color: white;
}

.close:active{
    color:red;
}

